import React, { Component } from "react"
import SEO from "../components/seo"
import styled from "@emotion/styled"
import Img from 'gatsby-image'
import { graphql, withPrefix } from "gatsby"
import Helmet from 'react-helmet'

export const MainImage = styled(Img)`
  max-height: 50vh;
  margin: 0 auto;
  @media only screen and (min-width: 1400px) {
    width: 1280px;
  }
`


export const query = graphql`
  query {
    mainBg: file(relativePath: { eq: "mainbg.png" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`

const L2DCanvas = styled.canvas`
  width: 40vh;
  height: 65vh;
`

//let live2dIsMounted = false;

class IndexPage extends Component {
  componentDidMount () {
    // if (live2dIsMounted===false){
    //   live2dIsMounted = true;
    //   const script = document.createElement("script");
    //   script.src = "lib/live2d/l2d.min.js";
    //   script.async = true;
    //   document.body.appendChild(script);
    // }
  }

  render() {
    return (
      <>
        <div>
          <SEO title="Home" />
          <h1>Welcome</h1>
          <p>This supposed to be my title</p>
          {/*<MainImage fluid={props.data.mainBg.childImageSharp.fluid}  imgStyle={{objectFit:'contain'}} />*/}
          {/*eslint-disable no-undef*/}
          <div id={'content'} className="main-content" >
            <div id={'l2d_canvas'} className="sub-content">
              <button hidden id={'btnChange'} className="active"></button>
              <L2DCanvas id={'glcanvas'} className="glcanvas"></L2DCanvas>
            </div>
          </div>
          {/*eslint-enable no-undef*/}
        </div>
        <Helmet>
          <script src={withPrefix('lib/live2d/l2d.min.js')} type="text/javascript" />
        </Helmet>
      </>
    )
  }
}

export default IndexPage
